import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef,  NgZone,  Optional,  ViewEncapsulation, } from "@angular/core";
import { TDSButtonGroupComponent } from "./button-group.component";
import { TDSButtonBase } from "./button.base";

@Component({
    selector: 'button[tds-button], a[tds-button]',
    exportAs: 'tdsButton',
    preserveWhitespaces: false,
    changeDetection: ChangeDetectionStrategy.OnPush,
    encapsulation: ViewEncapsulation.None,
    templateUrl: './button.component.html',
    host: {
        '[class]': 'cssClass',
        '[attr.tabindex]': 'disabled ? -1 : (tabIndex === null ? null : tabIndex)',
        '[attr.disabled]': 'disabled || null',
    }
})
export class TDSButtonComponent extends TDSButtonBase {   
    constructor(
        public ngZone: NgZone,
        public elementRef: ElementRef,
        public cdr: ChangeDetectorRef,
        @Optional() public _buttonGroup: TDSButtonGroupComponent) {
        super(ngZone, elementRef, cdr, _buttonGroup)
    }

}