    defaultClassCss(): string {
        let result = 'focus:outline-none box-border ring-opacity-20 dark:ring-opacity-20 focus:ring whitespace-nowrap border text-xl leading-5 relative';
        return result;
    }
    getClassCssBySize(size: ButtonSize): string {   
        let result: string = '';
        switch (size) {
            case "sm":
                result = 'p-1 h-sm';
                break;
            case "md":
                result = 'p-1.5 h-md';
                break;
            case "lg":
                result = 'p-2 h-lg';
                break;
            case "xl":
                result = 'p-2.5  h-xl';
                break;
        }
        return result;
    }
