import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnChanges,  OnInit, SimpleChanges, TemplateRef, ViewEncapsulation } from "@angular/core";
import { ButtonSize } from "tds-ui/core/config";

@Component({
    selector: 'button[tds-button-close], a[tds-button-close]',
    exportAs: 'tdsButtonClose',
    preserveWhitespaces: false,
    changeDetection: ChangeDetectionStrategy.OnPush,
    encapsulation: ViewEncapsulation.None,
    template: `
   <ng-container *tdsStringTemplateOutlet="iconTemplate"><i [class]="iconTemplate"></i></ng-container>  
    `,
    host: {
        '[class]': 'cssClass',
        '[attr.tabindex]': 'disabled ? -1 : (tabIndex === null ? null : tabIndex)',
        '[attr.disabled]': 'disabled || loading || null',
    }
})
export class TDSButtonCloseComponent implements OnChanges, OnInit {

    @Input() size: ButtonSize = 'xl';
    @Input() iconTemplate: string | TemplateRef<void> | null = 'tdsi-close-fill'
    cssClass: string = '';
    constructor(
        public cdr: ChangeDetectorRef) {
    }
    ngOnChanges(changes: SimpleChanges): void {
        this.buildCss();
        this.cdr.markForCheck();
    }
    ngOnInit(): void {
        this.buildCss();
    }
    buildCss() {
        this.cssClass = `${this.getClassCssBySize(this.size)} ${this.defaultClassCss()} `
    }
    private getClassCssBySize(size: ButtonSize): string {
        let result: string = '';
        switch (size) {
            case "sm":
                result = 'p-0 h-4';
                break;
            case "md":
                result = 'p-0.5 h-5';
                break;
            case "lg":
                result = 'p-1 h-6';
                break;
            case "xl":
                result = 'p-1.5 h-7';
                break;
        }
        return result;
    }
    private defaultClassCss(): string {
        let result = 'focus:outline-none box-border  whitespace-nowrap  border-0 text-base leading-4 relative text-neutral-1-500 dark:text-d-neutral-1-500 hover:bg-neutral-3-50 dark:hover:bg-d-neutral-3-300 hover:rounded-md inline-flex items-center justify-center';
        return result;
    }
}